@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: Poppins, sans-serif;
}

body {
	min-height: 100vh;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #dad3f8;
}

.container {
	max-width: 950px;
	width: 100%;
	margin: 0 20px;
	background-color: #fff;
	padding: 40px 50px 40px 40px;
	border-radius: 12px;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);

	& .main-title {
		font-size: 30px;
		font-weight: 500;
		margin-bottom: 20px;
	}

	& .content {
		display: flex;
		align-items: center;
		justify-content: space-between;

		& .text-content {
			width: 80%;
			height: 100%;

			& .text {
				display: none;
			}

			& .home {
				display: block;
			}

			& .title {
				font-size: 25px;
				font-weight: 500;
				margin-bottom: 10px;
			}

			& p {
				text-align: justify;
			}
		}
	}

	& .content .list {
		display: flex;
		flex-direction: column;
		position: relative;
		width: 20%;
		margin-right: 50px;

		& label {
			height: 60px;
			font-size: 22px;
			font-weight: 500;
			cursor: pointer;

			border-radius: 12px;
			line-height: 60px;
			padding-left: 25px;
			color: #333;
			z-index: 12;
			// background-color: red;

			&:hover {
				color: #6d50e2;
			}
		}
	}
}

.content {
	& .indicator {
		position: absolute;
		height: 60px;
		width: 100%;
		top: 0;
		left: 0;
		background-color: #6d50e2;
		border-radius: 12px;
		transition: all 400ms ease;
	}
}

#home:checked ~ .text-content .home,
#blog:checked ~ .text-content .blog,
#help:checked ~ .text-content .help,
#code:checked ~ .text-content .code,
#about:checked ~ .text-content .about {
	display: block;
}

#blog:checked ~ .text-content .home,
#help:checked ~ .text-content .home,
#code:checked ~ .text-content .home,
#about:checked ~ .text-content .home {
	display: none;
}

#home:checked ~ .list .home,
#blog:checked ~ .list .blog,
#help:checked ~ .list .help,
#code:checked ~ .list .code,
#about:checked ~ .list .about {
	color: #fff;
}

#home:checked ~ .list .indicator {
	top: 0;
}

#blog:checked ~ .list .indicator {
	top: 60px;
}

#help:checked ~ .list .indicator {
	top: 120px;
}

#code:checked ~ .list .indicator {
	top: 180px;
}

#about:checked ~ .list .indicator {
	top: 240px;
}

.container input[type="radio"] {
	display: none;
}
